/**
 * @class AltrpModelUpdater
 */
import Resource from "./Resource";

class AltrpModelUpdater {
  /**
   *
   * @param {string} modelName
   * @param {int} modelId
   */
  constructor(modelName, modelId){
    this.modelName = modelName;
    if(! modelId){
      console.error('   !');
    }
    this.modelId = modelId;
    this.data = null;
    this.subscribers = [];
    this.resource = new Resource({route: `/ajax/models/${this.modelName}`});
    this.updating = false;
  }

  /**
   *    
   * @param {function} callback
   */
  subscribeToUpdates(callback){
    this.subscribers.push(callback);
    if(this.updating){
      return;
    }
    if(this.data){
      this.callSubscribers();
    } else {
      this.updateData();
    }
  }

  /**
   *    
   */
  async updateData(){
    this.updating = true;
    this.data = await this.resource.get(this.modelId);
    this.updating = false;
    this.callSubscribers();
  }

  /**
   *    
   */
  callSubscribers(){
    this.subscribers.forEach(subscriber=>{
      subscriber(this.getData());
    });
  }

  /**
   *  (   )
   * @param callback
   */
  unsubscribe(callback){
    this.subscribers = _.remove(this.subscribers, (idx, item)=>{
      console.log(item);
      return callback === item;
    })
  }

  /**
   *   
   * @return {object | null}
   */
  getData(){
    return this.data;
  }
  /**
   *   
   * @param {{}} data
   * @return void
   */
  updateWithData(data){
    this.data = {...data};
    this.callSubscribers();
  }
}


export default AltrpModelUpdater